import React from 'react'

const List = props => (
    <ul>
        <table>
        {
            props.items.map((item,index) => 
                <tr>
                <li key={index}><td>{item}</td> <td><button>Delete</button></td></li>
                </tr>
            )
        }
        </table>
    </ul>
)

export default List